.palette {
  width: 30px;
  height: 25px;
}
.input_palette {
  margin-right: 10px;
}
.first_palette {
  display: flex;
  justify-content: flex-end;
  margin: 10px 0;

  .colorOne {
    background-color: $darkgreenblue;
  }
  .colorTwo {
    background-color: $dirtyblue;
  }
  .colorThree {
    background-color: $hospitalgreen;
  }
}
